.form__item{
    border: 0;
    margin-bottom: 5px;
    margin-top: 13px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    width: 100%;
    padding: 0;
}
.form__item:focus{
    outline: none;
}
.form__item::placeholder{
    border: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    opacity: 0.8
}

