.form__submit-button{
    background-color: black;
    width: 100%;
    height: 50px;
    margin-top: 48px;
    border-radius: 2px;
    color: white;
}
.form__submit-button:hover{
    background-color: rgba(0, 0, 0, 0.8);
    transition: background-color 1s linear;
    cursor: pointer;
}